@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
}

* {
  box-sizing: border-box;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  width: 100%;
  padding: 40px 15px;
}

.film{width:100%;position:relative;transition:all 0.3s ease-in-out}.film img{width:100%}.film .link__film{position:absolute;width:100%;left:0;bottom:0;background-color:rgba(0,0,0,0.5);z-index:1;display:flex;flex-direction:column;padding:10px;align-items:flex-start;justify-content:flex-end}.film .title__film{color:#ffffff;font-weight:700;font-size:18px}.film .release__date{color:#ffffff;font-size:14px}.film:hover{transform:scale(1.05)}


.movie__list{display:grid;grid-template-columns:repeat(5, 1fr);grid-template-rows:repeat(4, 1fr);grid-gap:20px;width:100%}

.search__wrapper{width:500px;margin:0 auto;margin-bottom:30px}

.character__list{background-color:#ffffff;border-radius:15px;padding:20px}.content__chars{display:flex;flex-direction:row;justify-content:flex-start;align-items:stretch;max-width:100%;overflow-x:scroll}.character{display:flex;flex-direction:column;align-items:flex-start;color:#333333;margin:0 5px}.character img{margin-bottom:5px}.character .name{font-weight:600;margin-bottom:5px}

.reviews__list{background-color:#ffffff;border-radius:20px;padding:20px;display:flex;flex-direction:column;align-items:flex-start}.review{color:#333333}.review .name{font-size:18px;font-weight:600;display:block;margin-bottom:10px}.review .descr{font-size:16px}

.movie__details-page{background-color:#333333;color:#ffffff}.movie__content{transform:translateY(-500px)}.banner{position:relative}.banner img{max-width:100%}.banner .filter{width:100%;height:100%;background-color:rgba(0,0,0,0.7);position:absolute;left:0;top:0}.tabs-btn{color:#ffffff}.MuiButton-containedSecondary{margin-right:15px}

